@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap);
*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],li,figure,figcaption,blockquote,dl,dd{margin:0}body{min-height:100vh;scroll-behavior:smooth;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font:inherit}@media (prefers-reduced-motion: reduce){*{-webkit-animation-duration:0.01ms !important;animation-duration:0.01ms !important;-webkit-animation-iteration-count:1 !important;animation-iteration-count:1 !important;transition-duration:0.01ms !important;scroll-behavior:auto !important}}.home__container{background:url(/recruitment-task-bpol/static/media/maria-ten.dd81c811.jpg) no-repeat top/cover;width:100%;height:100vh}.navbar__container{display:flex;justify-content:space-around;align-items:center;width:100%;height:15vh}.logo__image{max-width:100%;max-height:100%;width:auto;height:30vh;padding:10px;flex:1 1}.search__container{display:flex;justify-content:center;align-items:center;flex:1 1}.search__container .search__form{width:100%;position:relative;padding:10px}.search__container .search__form .search__input{transition:1s;box-shadow:2px 0px 44px 8px transparent;background:#FFE81F;border-radius:2rem;height:3rem;width:50%;border:none;padding:10px;font-family:"Do Hyeon",sans-serif}.search__container .search__form .search__input:focus{transition:0.3;outline:none;box-shadow:2px 0px 44px 8px #c5aa10}.search__container .search__form .search__button{transition:1s;box-shadow:2px 0px 44px 8px transparent;display:flex;justify-content:center;align-items:center;width:2.5rem;height:2.5rem;background:url(data:image/png;base64,) no-repeat center/contain;border:none;position:absolute;top:50%;left:50%;transform:translate(-120%, -50%)}.search__container .search__form .search__button:focus{outline:none;transition:0.3;box-shadow:0 0 18px 18px #FFE81F}.person-info__container{transition:0.3;display:flex;justify-content:space-evenly;align-items:center;background:rgba(32,31,36,0.116);margin:10px;font-family:"Do Hyeon",sans-serif}.person-info__container:hover{transition:0.3;background:rgba(130,127,144,0.116);cursor:pointer}.film-details__list{display:flex;flex-direction:column;background:#000;border:0.5rem solid #FFE81F;border-radius:2%;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);width:50%;height:50%;overflow:auto}.character-attended__span{color:#000;font-size:1.5rem;text-shadow:-1px 0 #FFE81F,0 1px #FFE81F,1px 0 #FFE81F,0 -1px #FFE81F;flex:1 1;font-family:"Do Hyeon",sans-serif}.page-changing__button{display:flex;justify-content:center;align-items:center;padding:1rem;background:rgba(32,31,36,0.116);color:#000;font-size:16px;text-shadow:-1px 0 #FFE81F,0 1px #FFE81F,1px 0 #FFE81F,0 -1px #FFE81F;flex:1 1;font-weight:bold;margin:0 10px;width:2rem;height:2rem}.page-changing__button:hover{transition:0.3;background:rgba(130,127,144,0.116);cursor:pointer}.page-changing__container{display:flex;position:absolute;bottom:10px;left:0}.film-details__list-element{display:flex;align-items:flex-start}.film-details__list-element .film-details__content{list-style:none;display:flex;color:white;text-align:center;padding:10px;font-family:"Do Hyeon",sans-serif;font-size:16px}.film-details__list-element .film-details__label{color:#000;font-size:1.5rem;text-shadow:-1px 0 #FFE81F,0 1px #FFE81F,1px 0 #FFE81F,0 -1px #FFE81F;flex:1 1;font-family:"Do Hyeon",sans-serif;font-size:16px;display:flex;justify-content:flex-start;align-items:center}.person-info__element{color:#000;font-size:1.5rem;text-shadow:-1px 0 #FFE81F,0 1px #FFE81F,1px 0 #FFE81F,0 -1px #FFE81F;flex:1 1;font-family:"Do Hyeon",sans-serif}

